@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800&display=swap');

body {
  margin: 0;
  background-color: #FCFCFF; 
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
  /* line-height: 35px; */
}

/* html {
  scroll-behavior: smooth
} */

.Project {
  font-size: 1rem;
}

#home-illus {
  width: 90%;
  align-self: center;
  margin: .5rem 0 1rem 0;
  /* border-color: purple; */
}

#arrow {
  width: 2.5%;
  margin: 0 0 4rem 0;
  animation: bounce 2s infinite;
}

#arrow:hover {
  width: 2.6%;
}

@keyframes bounce {
  50% {
      transform: translateY(-10px);
    }
  }

.landing {
  display: flex;
  flex-direction: column;
  width: 51%;
  margin: auto;
  /* margin: 7rem 10rem 0 10rem; */
  /* background-color:white; */
}

.big-header {
  font-size: 2rem;
  text-align: left;
  margin: 10rem 0 0.75rem 0;
  /* background-color: red; */
}

.paragraph {
  font-size: 1.125rem;
  text-align: left;
  line-height: 1.75rem;
  /* background-color: blue; */
}

.button {
  margin-top: 1.5rem;
  font-size: 1rem;
  background-color: #034D57;
  color: #FCFCFF; 
  border: 0;
  padding: .65rem 1.25rem .65rem 1.25rem;
  border-radius: 3rem;
}

.button:hover {
  background-color: #FCFCFF;
  color: #034D57; 
  border: solid;
  border-width: 1px;
  border-color: #7a8e91;
  cursor: pointer;
}

.NavBar {
  position: fixed;
  padding: 2% 0 0 76%;
  background-color: rgb(252, 252, 252, .9);
}

#leaf-icon {
  width: 10%;
  margin-right: 500px;
}

.nav-row {
  margin: 1rem 3rem 2rem 0;
}

.nav-button {
  border: none;
  background-color: transparent;
  font-family: 'Inter', sans-serif;
  margin: 0 0 0 1.5rem;
  font-size: .875rem;
  letter-spacing: .25px;
  font-weight: 500;
  color: #787878;
}

.nav-button:hover {
  /* text-decoration: underline;
  text-underline-offset: 4px;
  text-decoration-color: #034D57; 
  text-decoration-thickness: 2px; */
  color: #000000;
  cursor: pointer;
}

.nav-button-bold {
  border: none;
  background-color: transparent;
  font-family: 'Inter', sans-serif;
  margin: 0 0 0 1.5rem;
  font-size: .875rem;
  font-weight: 500;
  letter-spacing: .25px;
  text-decoration: underline;
  text-underline-offset: 4px;
  text-decoration-color: #034D57; 
  text-decoration-thickness: 2px;
}

.nav-button-bold:hover {
  text-decoration: underline;
  text-underline-offset: 4px;
  text-decoration-color: #034D57; 
  text-decoration-thickness: 2px;
  cursor: pointer;
}

#work {
  display: flex;
  width: 60%;
  flex-direction: row;
  align-items: flex-start;
  /* width: 51%; */
  font-size: 1.5rem;
  margin: auto;
  padding: 2rem 0 0 0;
  margin-bottom: 2rem;
  /* text-align: left;
  margin: 0 0 2rem 12rem; */
}

.grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
  margin: auto;
  flex-wrap: wrap;
}

.grid-row {
  display: flex;
  flex-direction: row;
  margin: 0 0 3rem 0;
}

.grid-img {
  width: 100%;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.gcard-lwrap {
  display: flex;
  flex-direction: column;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 0px 0px 8px 2px rgba(150, 150, 150, 0.2);
  background-color: #ffffff;
  border-radius: 7px;
  margin: 0 3rem 0 0;
}

.gcard-lwrap:hover {
  box-shadow: 0px 0px 10px 4px rgba(150, 150, 150, 0.2);
  cursor: pointer;
}

.gcard-rwrap {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 8px 2px rgba(150, 150, 150, 0.2);
  background-color: #ffffff;
  border-radius: 7px;
}

.gcard-rwrap:hover {
  box-shadow: 0px 0px 10px 4px rgba(150, 150, 150, 0.2);
  cursor: pointer;
}

.gcard-label {
  padding: 2rem 2rem 2rem 2rem;
  text-align: left;
}

.gcard-heading {
  /* font-size: .9rem; */
  font-weight: 600;
  letter-spacing: .25px;
  margin: 0 0 .5rem 0;
}

.gcard-text {
  line-height: 1.4rem;
}

.footer {
  height: 3rem;
  color: #b7b7b7;
  padding: 7rem 0 0 0;
  font-size: .875rem;  
}

/* CASE STUDY */
.project-wrap {
  display: flex;
  flex-direction: column;
  width: 55%;
  margin: auto;
}

.project-header {
  font-size: 2rem;
  text-align: left;
  margin: 9rem 0 2rem 0;
  /* text-decoration: underline;
  text-underline-offset: 12px;
  text-decoration-color: #034D57; 
  text-decoration-thickness: 2px; */
}

.project-info {
  font-size: 1rem;
  text-align: left;
  margin: 0 0 2rem 0;
  min-width: 125%;
}

.section-flex {
  display: flex;
  flex-direction: row;
}

.section-lflex {
  display: flex;
  flex-direction: column;
  max-width: 70%;
}

.section-rflex {
  display: flex;
  flex-direction: column;
}

.section-header {
  font-size: 1.25rem;
  text-align: left;
  margin: 1rem 0 1.5rem 0;
}

.subsection-header {
  font-size: .875rem;
  letter-spacing: .5px;
  font-weight: 600;
  text-align: left;
  margin: 0 0 0.75rem 0;
  color: #033E57;
}

.subsection-header-sec {
  font-size: .875rem;
  letter-spacing: .5px;
  font-weight: 600;
  text-align: left;
  margin: 4rem 0 0.75rem 0;
  color: #033E57;
}

.subsection-rheader-top {
  font-size: .875rem;
  letter-spacing: .5px;
  font-weight: 600;
  text-align: left;
  margin: 3rem 0 0.5rem 1rem;
  color: #033E57;
}

.subsection-rheader {
  font-size: .875rem;
  letter-spacing: .5px;
  font-weight: 600;
  text-align: left;
  margin: 0 0 0.5rem 1rem;
  color: #033E57;
}

.project-text {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  margin: 0 0 3rem 0;
}

.project-text-single {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  margin: 0 0 0 0;
}

.project-text-low {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  margin: 0 0 2rem 0;
}

.project-ltext {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  margin: 0 7rem 4rem 0;
}

.project-rtext {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  margin: 0 0 3rem 1rem;
}

.list-text {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  margin: 0 0 1rem 0;
}

ul {
  /* max-width: 80%; */
  margin: .75rem 0 .75rem 0;
  padding: 0 0 0 1rem;
}

.project-img {
  width: 100%;
  border-radius: 7px;
  margin: 0 0 3rem 0;
}

.cap-card {
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  display: flex;
  flex-direction: row;
  box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  /* padding: 1.5rem 1.5rem 1.5rem 3rem; */
  margin: 0 0 3rem 0;
}

.cap {
  background-color: #033E57;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  max-width: .75%;
}

.cap-card-text {
  font-size: 1rem;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  padding: 1.75rem 1.5rem 1.75rem 3rem;
  /* font-style: italic; */
}

.how-might-we {
  font-size: 1rem;
  font-weight: 500;
  text-align: left;
  line-height: 1.55rem;
  color: #2c2c2c;
  padding: 1.75rem 1.5rem 1.75rem 3rem;
  /* font-style: italic; */
}

.empty {
  opacity: 0;
}

.graphs {
  background-color: #F4FAFF;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  margin: 1rem 0 0 0;
  flex-wrap: wrap;
}

.graphgrid-row {
  display: flex;
  flex-direction: row;
  /* margin: 4rem 0 3rem 4rem; */
  margin: auto;
  max-width: 85%;
}

.card-lgraph {
  display: flex;
  flex-direction: column;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  margin: 4rem 3rem 3rem 3rem;
  max-width: 45%;
}

.card-rgraph {
  display: flex;
  flex-direction: column;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  margin: 4rem 3rem 3rem 0;
  max-width: 45%;
}

.card-lgraph-low {
  display: flex;
  flex-direction: column;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  margin: 0 3rem 4rem 3rem;
  max-width: 45%;
}

.card-rgraph-low {
  display: flex;
  flex-direction: column;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  margin: 0 3rem 4rem 0;
  max-width: 45%;
}

.graphcard-label {
  font-size: .75rem;
  font-weight: 600;
  letter-spacing: .25px;
  text-align: left;
  padding: 2.75rem 2rem 1rem 3rem;
}

.graphcard-ques {
  padding: 0 5rem 2rem 3rem;
  text-align: left;
  line-height: 1.5rem;
}

.graphkey-wrap {
  display: flex;
  flex-direction: row;
}

.graph-img {
  width: 70%;
  padding: 0 0 2.75rem 3rem;
}

.affinity {
  background-color: #F4FAFF;
  display: flex;
  flex-direction: row;
  padding: 4rem 2rem 4rem 2rem;
  /* background-color: red; */
  /* flex-wrap: wrap; */
}

.categories-wrap {
  display: flex;
  flex-direction: row;
  margin: auto;
  /* background-color: aquamarine; */
  flex-wrap: wrap;
}

.category-flex {
  display: flex;
  flex-direction: column;
  margin: 0 5rem 0 0;
  align-items: center;
  /* background-color: blue; */
}

.category-rflex {
  display: flex;
  flex-direction: column;
  margin: 0 0 0 0;
}

.category-name {
  font-size: .75rem;
  letter-spacing: .5px;
  font-weight: 600;
  text-align: center;
  color: #033E57;
  margin: 0 0 1.5rem 0;
}

.post-it-row {
  display: flex;
  flex-direction: row;
  margin: 0 0 1.5rem 0;
}

.lpost-it {
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  /* padding: 3rem 3rem 3rem 3rem; */
  margin: 0 1rem 0 0;
  width: 11rem;
  height: 11rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.rpost-it {
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.03);
  background-color: #ffffff;
  border-radius: 7px;
  /* padding: 3rem 3rem 3rem 3rem; */
  width: 11rem;
  height: 11rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.post-it-text-short {
  font-size: .75rem;
  /* padding: 1.5rem; */
  min-width: 80%;
  /* background-color: aliceblue; */
}

.post-it-text {
  font-size: .75rem;
  max-width: 60%;
  line-height: .95rem;
}

/* .li {
  padding: 0;
} */
/* TYPING ANIMATION */
.typing-header {
  animation: typing 2s steps(19), blink .5s step-end infinite alternate;
  width: 15ch;
  white-space: nowrap;
  overflow: hidden;
  border-right: 2px solid;
  /* font-family: monospace; */
  font-size: 2em;
}

@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}
